@import url('https://fonts.googleapis.com/css2?family=Bona+Nova&display=swap');
*{
    padding: 0;
    margin: 0;
    font-family: 'Bona Nova', serif;
}
body{
    background-color: #ffba08;
}
.main{
    width: 80%;
    margin: 0 auto;
    text-align: center;
}
.heading h1{
    color:#03071e;
    font-size: 48px;
    font-weight: 900;
    letter-spacing: 1px;
    padding-top: 2rem;
    margin: 1rem;
}
.search-bar{
    margin: 1rem 0;
    width: 400px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 1rem;
    padding: 0.3rem 1rem;
}
.container{
    width: 80%;
    margin: 25px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(150px,1fr));
    grid-gap:2rem
}
.card{
    width:180px;
    border-radius: 1rem;
    background-color: #03071e;
    padding: 5px;
}

img{
    width:100%;
    border-radius: 1rem 1rem 0 0;
}
.card h3{
    color: white;
    font-size: 18px;
}
.indexContainer{
    display: flex;
    margin: 2.5rem;
}
.numBox{
    width: 70px;
    height: 30px;
    background-color: #03071e;
    display: flex;
    justify-content: center;
    border-radius: 5px;
    align-items: center;
    color: white;
    margin: 0 5px 0 5px;
}
.numBox:hover{
    opacity: 0.5;
    color:white;
}
.content img{
    width: 100%;
    height: 600px;

}
.content .inner-content{
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: rgba(0, 0, 0, 0.39);
    color: white;
    font-size: 1.5rem;
    padding: 2rem;
    text-align: center;
    box-shadow: 0 5px 5px rgba(0,0,0,0.5);
}
.recipe-details{
    width: 70%;
    margin: 2rem auto;
    display: flex;
    justify-content: space-between;
    
    align-items: center;
}
.ingredients,.instructions{
    flex-basis: 50%;
    text-align: justify;
  

}
.ingredients h4{
    margin-top: 1.5rem;
    font-weight: bold;
}
.iframe{
    height: 515px;
    width: 100%;
}
.videos{
 
    width: 80%;
    
    margin: 3rem auto;
    display: flex;
    flex-direction: column;
}